/* 全局样式 */
.App {
  text-align: center;
}

/* 基础部分样式 */
.section {
  margin-bottom: 4rem;
  position: relative;
}

/* 英雄区域样式 */
.hero-section {
  background: linear-gradient(135deg, #0085b3 0%, #3a72d9 100%);
  color: white;
  padding: 3rem 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 30px rgba(0, 133, 179, 0.3);
}

/* 卡片网格布局 */
.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

/* 现代化元素 */
.gradient-text {
  background: linear-gradient(90deg, #0085b3, #c60077);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
  font-weight: bold;
}

.glass-effect {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(0, 133, 179, 0.2);
  border-radius: 16px;
}

.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 133, 179, 0.3);
}

/* 动画效果 */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes neonPulse {
  0% { box-shadow: 0 0 10px rgba(0, 133, 179, 0.5), 0 0 20px rgba(0, 133, 179, 0.3); }
  50% { box-shadow: 0 0 15px rgba(0, 133, 179, 0.7), 0 0 30px rgba(0, 133, 179, 0.5); }
  100% { box-shadow: 0 0 10px rgba(0, 133, 179, 0.5), 0 0 20px rgba(0, 133, 179, 0.3); }
}

@keyframes glowingBorder {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes upDown {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

@keyframes zoomInOut {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes morphBlob {
  0% {
    border-radius: 40% 60% 60% 40% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 70% 70% 30% / 40% 50% 50% 60%;
  }
  100% {
    border-radius: 60% 40% 40% 60% / 50% 60% 40% 50%;
  }
}

@keyframes computerLinesAnimation {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}

.animate-in {
  animation: fadeIn 0.8s ease forwards;
}

.neon-element {
  animation: neonPulse 2s infinite;
  border: 1px solid rgba(0, 133, 179, 0.3);
}

.rotate-slow {
  animation: rotate 20s linear infinite;
}

.rotate-medium {
  animation: rotate 10s linear infinite;
}

.rotate-fast {
  animation: rotate 5s linear infinite;
}

.float-animation {
  animation: upDown 6s ease-in-out infinite;
}

.pulse-animation {
  animation: zoomInOut 3s ease-in-out infinite;
}

/* AI风格装饰元素 */
.ai-decoration {
  position: absolute;
  z-index: 0;
  filter: blur(40px);
  opacity: 0.2;
}

.ai-decoration-1 {
  width: 300px;
  height: 300px;
  background: linear-gradient(135deg, #0085b3 0%, #c60077 100%);
  top: -150px;
  right: -100px;
  border-radius: 40% 60% 60% 40% / 60% 30% 70% 40%;
  animation: morphBlob 12s ease-in-out infinite alternate;
}

.ai-decoration-2 {
  width: 200px;
  height: 200px;
  background: linear-gradient(135deg, #c60077 0%, #0085b3 100%);
  bottom: -100px;
  left: -50px;
  border-radius: 60% 40% 30% 70% / 40% 50% 50% 60%;
  animation: morphBlob 15s ease-in-out infinite alternate;
}

/* 技术线条 */
.tech-lines {
  position: absolute;
  inset: 0;
  background-image: 
    linear-gradient(to right, rgba(0, 133, 179, 0.1) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(0, 133, 179, 0.1) 1px, transparent 1px);
  background-size: 40px 40px;
  mask-image: radial-gradient(circle at center, black 30%, transparent 80%);
  animation: computerLinesAnimation 40s linear infinite;
  z-index: 0;
  opacity: 0.6;
  pointer-events: none;
}

/* 光速扫描线 */
.scan-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 6px;
  background: linear-gradient(90deg, 
    rgba(0, 133, 179, 0) 0%, 
    rgba(0, 133, 179, 0.5) 50%, 
    rgba(0, 133, 179, 0) 100%
  );
  opacity: 0.5;
  z-index: 2;
  animation: scanAnimation 3s linear infinite;
  pointer-events: none;
}

@keyframes scanAnimation {
  0% { top: -5px; opacity: 0; }
  5% { opacity: 0.5; }
  95% { opacity: 0.5; }
  100% { top: 100%; opacity: 0; }
}

.tech-circuit {
  position: absolute;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 133, 179, 0.5), transparent);
  z-index: 0;
}

.tech-circuit-1 {
  width: 150px;
  top: 30%;
  left: 0;
}

.tech-circuit-2 {
  width: 300px;
  top: 60%;
  right: 0;
}

.tech-dot {
  position: absolute;
  width: 6px;
  height: 6px;
  background-color: rgba(0, 133, 179, 0.8);
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 133, 179, 0.8);
}

/* 数据流效果 */
.data-stream {
  position: absolute;
  width: 2px;
  background: linear-gradient(to bottom, transparent, rgba(0, 133, 179, 0.8), transparent);
  opacity: 0;
  z-index: 0;
  pointer-events: none;
}

.data-stream-1 {
  height: 100px;
  left: 20%;
  top: 10%;
  animation: dataStream 3s infinite 1s;
}

.data-stream-2 {
  height: 150px;
  left: 40%;
  top: 30%;
  animation: dataStream 4s infinite 0.5s;
}

.data-stream-3 {
  height: 120px;
  left: 70%;
  top: 20%;
  animation: dataStream 2.5s infinite 2s;
}

.data-stream-4 {
  height: 180px;
  left: 85%;
  top: 40%;
  animation: dataStream 3.5s infinite 0.2s;
}

@keyframes dataStream {
  0% { transform: translateY(-100%); opacity: 0; }
  20% { opacity: 0.8; }
  100% { transform: translateY(300%); opacity: 0; }
}

/* 响应式调整 */
@media (max-width: 768px) {
  .hero-section {
    padding: 2rem 0;
  }
  
  .ai-decoration-1, .ai-decoration-2 {
    width: 150px;
    height: 150px;
  }
}

/* 现代化滚动条 */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #eef5ff;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #0085b3;
  border-radius: 4px;
  opacity: 0.7;
}

::-webkit-scrollbar-thumb:hover {
  background: #00a5dd;
}

/* 科技感数据网格背景 */
.cyber-grid {
  position: relative;
}

.cyber-grid::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    linear-gradient(rgba(0, 133, 179, 0.05) 1px, transparent 1px),
    linear-gradient(90deg, rgba(0, 133, 179, 0.05) 1px, transparent 1px);
  background-size: 20px 20px;
  z-index: 0;
  pointer-events: none;
}

/* 确保导航菜单中的文本是白色（可见） */
.MuiAppBar-root a, 
.MuiAppBar-root button,
.MuiDrawer-root a,
.MuiDrawer-root button {
  color: inherit;
  text-decoration: none;
}

.MuiAppBar-root .MuiButtonGroup-root button {
  color: white;
}

/* 确保按钮文本可见 */
button.MuiButton-containedPrimary,
button.MuiButton-containedSecondary {
  color: white;
  font-weight: 500;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

/* 增强页面标题可见性 */
h1, h2, h3, h4, h5, h6 {
  color: #121c42;
  text-shadow: 0 1px 3px rgba(255, 255, 255, 0.5);
}

/* 增强页面内容可见性 */
p, span, div {
  color: #3a456c;
}

/* 增强链接可见性 */
a.MuiLink-root {
  color: #0085b3;
  font-weight: 500;
}

a.MuiLink-root:hover {
  color: #006591;
  text-decoration: none;
}

/* 确保hero区域文本颜色 */
.hero-section h1,
.hero-section h2,
.hero-section h3,
.hero-section p,
.hero-section div {
  color: white;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
}

/* 页面内元素入场动画 */
.fade-in-up {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.fade-in-up.visible {
  opacity: 1;
  transform: translateY(0);
}

.staggered-fade-in > * {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.4s ease-out, transform 0.4s ease-out;
}

.staggered-fade-in > *:nth-child(1) {
  transition-delay: 0.1s;
}

.staggered-fade-in > *:nth-child(2) {
  transition-delay: 0.2s;
}

.staggered-fade-in > *:nth-child(3) {
  transition-delay: 0.3s;
}

.staggered-fade-in > *:nth-child(4) {
  transition-delay: 0.4s;
}

.staggered-fade-in > *:nth-child(5) {
  transition-delay: 0.5s;
}

.staggered-fade-in > *:nth-child(6) {
  transition-delay: 0.6s;
}

.staggered-fade-in.visible > * {
  opacity: 1;
  transform: translateY(0);
}

/* 圆形弧光边框 */
.glow-border {
  position: relative;
  overflow: hidden;
}

.glow-border::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: conic-gradient(
    transparent, 
    rgba(0, 133, 179, 0.4), 
    rgba(198, 0, 119, 0.4),
    rgba(0, 133, 179, 0.4), 
    transparent
  );
  animation: rotate 6s linear infinite;
  z-index: -1;
}

.glow-border::after {
  content: '';
  position: absolute;
  inset: 3px;
  background: white;
  border-radius: inherit;
  z-index: -1;
}
